$font-path:"./fonts/";

@font-face {
	font-family: 'FreeSans';
	src: url('#{$font-path}FreeSans.ttf') format('truetype');
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: 'FreeSansBold';
	src: url('#{$font-path}FreeSansBold.ttf') format('truetype');
	font-weight: normal;
	font-style: normal;
}

$freesans: 'FreeSans',Helvetica,Arial,sans-serif;
$freesans-bold: 'FreeSansBold',Helvetica,Arial,sans-serif;

$font-family : (
	'regular' : ( 'family': $freesans, 'weight': 'normal', 'letter-spacing': 'normal' ),
	'bold' : ( 'family': $freesans-bold, 'weight': 'normal', 'letter-spacing': -1px )
);

@mixin font-family( $family ){
	$font : map-get($font-family, $family);

	font-family: map-get( $font, 'family' );
	font-weight: map-get( $font, 'weight' );
	letter-spacing: map-get( $font, 'letter-spacing' );
}