@import "../../style/index";

$background-color--primary: #1E1E1D;
$background-color--secondary: #565A5C;
$font-color--primary: #ffffff;
$font-color--secondary: #eeeeee;

$red: #c60c30;
$blue: #00a1de;

$gradiant-transition-time: 8s;

@mixin gradiant-background($color1, $color2, $transition-time) {

	background: linear-gradient(270deg, $color1, $color2);
	background-size: 200% 200%;

	-webkit-animation: AnimationName $transition-time ease infinite;
	-moz-animation: AnimationName $transition-time ease infinite;
	animation: AnimationName $transition-time ease infinite;

	@-webkit-keyframes AnimationName {
		0%{background-position:0% 50%}
		50%{background-position:100% 50%}
		100%{background-position:0% 50%}
	}
	@-moz-keyframes AnimationName {
		0%{background-position:0% 50%}
		50%{background-position:100% 50%}
		100%{background-position:0% 50%}
	}
	@keyframes AnimationName {
		0%{background-position:0% 50%}
		50%{background-position:100% 50%}
		100%{background-position:0% 50%}
	}

}

.ctaApp {
	width: 100vw;
	height: 100vh;
	overflow-y: scroll;
	@include gradiant-background($red, $blue, $gradiant-transition-time);
	color: $font-color--primary;
	padding-top: 0.5rem;

	@include font-family(regular);

	&Child {
		margin: 0.5rem;
		padding: 0.5rem;
		background-color: rgba(white, 0.15);
		backdrop-filter: blur(5px);

		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: space-between;

		&:first-of-type {
			margin-top: 0;
		}

		&Left {

		}

		&Right {
			text-align: right;
		}

		&Large {
			font-size: 31px;
			@include font-family(bold);
		}
		&Small {
			font-size: 1rem;
			color: $font-color--secondary;
		}
	}
}